<template>
  <div class>
    <!--头部-->
    <div class="header">
      <div class="hTitle">Informal Essay</div>
      <!--下拉列表-->
      <ul class="hMenu">
        <router-link v-for="(item, index) in route" :key="index" :to="{path: item.path}">
          <li class="menu">{{item.title}}</li>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
import route from '../router/config'
export default {
  data () {
    return {
      route: route
    };
  },
  mounted () {},
  methods: {},
  watch: {},
  computed: {},
}
</script>
<style scoped>
</style>


