<template>
  <div id="app">
    <navigation></navigation>
    <keep-alive>
      <router-view/>
    </keep-alive>
    <!-- <foot></foot> -->
  </div>
</template>

<script>
import navigation from './components/navigation'
import foot from './components/foot'
export default {
  name: 'App',
  components: {
    navigation,
    foot
  }
}
</script>

<style>
#app{
  position: relative;
  min-height: 100vh;
}
</style>



